AEF,yBACE,uBACE,eAAA,CACA,kBAAA,CAAA","sourcesContent":["/**\r\n * Cannabis Data Style\r\n * Copyright (c) 2022 Cannabis Data\r\n * Copyright (c) 2021-2022 Cannlytics\r\n *\r\n * Authors:\r\n *     Keegan Skeate <https://github.com/keeganskeate>\r\n * Created: 10/4/2020\r\n * Updated: 11/22/2022\r\n * License: MIT License <https://github.com/cannabisdata/cannabisdata/blob/main/LICENSE>\r\n */\r\n@import '~bootstrap/scss/bootstrap';\r\n@import url('https://fonts.googleapis.com/css?family=Source+Serif+Pro:100,200,300,400,500,600,700');\r\n@import url('https://fonts.googleapis.com/css?family=Libre+Franklin:100,200,300,400,500,600,700');\r\n@import url('https://fonts.googleapis.com/css?family=Libre+Baskerville:100,200,300,400,500,600,700');\r\n@import url(\"https://fonts.googleapis.com/css?family=Cinzel:400\");\r\n@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300&display=swap');\r\n@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700');\r\n@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@200;300;400;600&display=swap');\r\n\r\n/*--------------------------------------------------\r\n * Body\r\n *------------------------------------------------*/\r\n \r\n/* Body */\r\nhtml, body {\r\n  height: 100%;\r\n}\r\nbody {\r\n  margin: 0;\r\n  background-color: #fff;\r\n  font-size: .875rem;\r\n}\r\n\r\n/* Dark pages */\r\nbody.dark,\r\nbody.dark #page {\r\n  background: rgb(50, 52, 67);\r\n}\r\n\r\n/* Dark layout */\r\nbody:not(.dark) .dark-only {\r\n  display: none;\r\n}\r\nbody.dark .light-only {\r\n  display: none;\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Text\r\n *------------------------------------------------*/\r\n\r\n/* Text fonts */\r\n* {\r\n  font-family: 'IBM Plex Sans', 'Source Serif Pro', Georgia, 'Times New Roman', Times, serif !important;\r\n}\r\nh1, h2, h3, h4, h5, h6 {\r\n  font-family: 'Cormorant Garamond', serif;\r\n}\r\np {\r\n  font-family: 'Libre Baskerville', serif !important;\r\n}\r\n.serif {\r\n  font-family: 'Libre Baskerville' !important;\r\n}\r\n.simple-serif {\r\n  font-family: 'Cormorant Garamond', serif !important;\r\n}\r\n.fancy-serif {\r\n  font-family:  'Playfair Display', 'serif' !important;\r\n}\r\n.sans-serif {\r\n  font-family: 'IBM Plex Sans', sans-serif;\r\n}\r\n.simple-sans-serif {\r\n  font-family: 'Libre Franklin';\r\n}\r\n.display-2,\r\n.display-3,\r\n.display-4 {\r\n  font-family: 'Libre Baskerville' !important;\r\n}\r\n\r\n/* Text formatting */\r\na {\r\n  text-decoration: none;\r\n}\r\n.bold {\r\n  font-weight: 600 !important;\r\n}\r\n.underline {\r\n  text-decoration: underline;\r\n}\r\n.no-underline {\r\n  text-decoration: none !important;\r\n}\r\n.text-no-wrap {\r\n  white-space: nowrap;\r\n}\r\n\r\n// .lead {\r\n//   font-family: 'Libre Baskerville', serif !important;\r\n//   line-height: 2 !important;\r\n// }\r\n// .lead a {\r\n//   font-family: 'Libre Baskerville', serif !important;\r\n//   line-height: 2 !important;\r\n// }\r\n// .brand {\r\n//   display: flex;\r\n//   align-items: flex-end;\r\n// }\r\n// .brand-font {\r\n//   font-family: 'Cinzel';\r\n//   letter-spacing: 0.5px;\r\n// }\r\n// body.dark .brand-font {\r\n//   color: #f5f5f5 !important;\r\n// }\r\n// .brand-title {\r\n//   font-family: 'Cinzel' !important;\r\n//   letter-spacing: 0.5px;\r\n//   font-size: 1.75rem;\r\n//   line-height: 1;\r\n//   font-weight: 100;\r\n//   margin: 0;\r\n// }\r\n// .brand-copyright {\r\n//   font-size: 8px;\r\n// }\r\n// .brand-title-small {\r\n//   font-size: 1.5rem;\r\n// }\r\n\r\n/* Text sizes */\r\np {\r\n  margin-bottom: 0;\r\n  font-size: 1.25rem;\r\n}\r\n.medium-text {\r\n  font-size: 1.15rem;\r\n}\r\n.tiny-text {\r\n  font-size: 0.8rem;\r\n}\r\n\r\n/* Text line heights */\r\np, ul {\r\n  line-height: 1.5;\r\n}\r\n.double-line {\r\n  line-height: 1.75 !important;\r\n}\r\n.no-line-height {\r\n  line-height: unset !important;\r\n}\r\n.large-line {\r\n  line-height: 1.25 !important;\r\n}\r\n.lh-condensed {\r\n  line-height: 1.25;\r\n}\r\n\r\n/* Dark text */\r\nbody.dark p,\r\nbody.dark h1,\r\nbody.dark h2,\r\nbody.dark h3,\r\nbody.dark h4,\r\nbody.dark h5,\r\nbody.dark h6 {\r\n  color: #ccc;\r\n}\r\nbody.dark p strong,\r\nbody.dark .text-dark {\r\n  color: #f8f8f8 !important;\r\n}\r\nbody.dark ol,\r\nbody.dark ul {\r\n  color: #ccc !important;\r\n}\r\nbody.dark .brand-title,\r\nbody.dark .navbar-toggler-icon:hover {\r\n  color: #fff !important;\r\n}\r\nbody.dark .cannlytics-green-text:hover svg {\r\n  fill: #96e6a1;\r\n}\r\nbody.dark ::-moz-selection {\r\n  color: #fff;\r\n}\r\nbody.dark ::selection {\r\n  color: #fff;\r\n}\r\nbody.dark .text-muted {\r\n  color: #ccc !important;\r\n}\r\nbody.dark .text-danger {\r\n  color: #ffa600 !important;\r\n}\r\nbody.dark .text-danger:hover {\r\n  color: #FF9500 !important;\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Markdown\r\n *------------------------------------------------*/\r\n\r\n.markdown h1 {\r\n  font-weight: 600;\r\n}\r\n.markdown h2 {\r\n  margin-top: 3rem;\r\n  margin-bottom: 1rem;\r\n  font-weight: 600;\r\n}\r\n.markdown h3 {\r\n  margin-top: 2rem;\r\n  margin-bottom: 1rem;\r\n  font-size: 1.5rem;\r\n  font-weight: 600;\r\n}\r\n.markdown h4 {\r\n  margin-top: 2rem;\r\n  margin-bottom: 1rem;\r\n  font-size: 1.25rem;\r\n  font-weight: 600;\r\n}\r\n.markdown p {\r\n  font-size: 1rem;\r\n  margin-top: 1.5rem;\r\n  line-height: 2;\r\n}\r\n.markdown p a,\r\n.markdown li a {\r\n  font-size: 1rem;\r\n}\r\n.markdown ul,\r\n.markdown ol {\r\n  margin-top: 1rem;\r\n}\r\n.markdown li {\r\n  font-size: 1rem;\r\n  margin-bottom: .5rem;\r\n}\r\n.markdown blockquote {\r\n  padding-left: 1.5rem;\r\n}\r\n.markdown p a,\r\n.markdown li,\r\n.markdown li a,\r\n.markdown em,\r\n.markdown strong {\r\n  font-family: 'Libre Baskerville' !important;\r\n}\r\n\r\n/* Paragraph anchors */\r\n.markdown h1 a,\r\n.markdown h2 a,\r\n.markdown h3 a,\r\n.markdown h4 a {\r\n  color: #2daf8e;\r\n  visibility: hidden;\r\n  text-decoration: none;\r\n  margin-left: .5rem;\r\n  padding: 0 .25rem;\r\n}\r\n.markdown h1:hover a,\r\n.markdown h2:hover a,\r\n.markdown h3:hover a,\r\n.markdown h4:hover a {\r\n  visibility: visible;\r\n}\r\n\r\n/* Markdown links */\r\n.markdown a {\r\n  background-color: transparent;\r\n  color: inherit;\r\n  text-decoration: none;\r\n  transition: background .15s cubic-bezier(.33, .66, .66, 1);\r\n  font-weight: 700;\r\n  overflow-wrap: break-word;\r\n  word-break: break-word;\r\n  word-wrap: break-word;\r\n}\r\n.markdown a:not(.headerlink):hover {\r\n  background-color: #50fa7b33;\r\n}\r\nbody.dark .markdown a {\r\n  text-decoration-color: #ffa600;\r\n}\r\nbody.dark .markdown a:hover {\r\n  background-color: #ffa60033;\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Actions\r\n *------------------------------------------------*/\r\n\r\n/* Action colors */\r\nmain .action {\r\n  color: #0BA67F !important;\r\n}\r\nbody.dark main .action {\r\n  color: #50fa7b !important;\r\n}\r\nbody.dark .btn-light {\r\n  background-color: #333644;\r\n  border-color: #495C65;\r\n}\r\nbody.dark .btn-light:hover {\r\n  background-color: #33364480\r\n}\r\n\r\n/* Action styles */\r\n.app-action {\r\n  cursor: pointer !important;\r\n}\r\n.app-no-action {\r\n  cursor: default !important;\r\n}\r\n.simple-action {\r\n  text-decoration: none;\r\n}\r\n.copy-btn {\r\n    float: right;\r\n    width: 0px;\r\n    padding: 2px 8px;\r\n    transform: translateX(-1rem);\r\n    cursor: pointer;\r\n}\r\n.codehilite:hover .copy-btn {\r\n  display: block;\r\n}\r\n.sliding-action {\r\n  display: inline-block;\r\n  text-decoration: none;\r\n  padding-bottom: 0px;\r\n}\r\n.sliding-action:hover {\r\n  color: #e53a23 !important;\r\n}\r\nbody.dark .sliding-action:hover {\r\n  color: #ffa600 !important;\r\n}\r\n.sliding-action:after {\r\n  content: '';\r\n  display: block;\r\n  margin: auto;\r\n  height: 3px;\r\n  width: 0px;\r\n  margin-top: 3px;\r\n  background: transparent;\r\n  transition: width .2s ease, background-color .2s ease;\r\n}\r\n.sliding-action:hover:after {\r\n  width: 100%;\r\n  background: #ffa600;\r\n}\r\n.btn-close {\r\n  position: absolute;\r\n  right: 1rem;\r\n  top: 1rem;\r\n}\r\n.btn-outline-primary {\r\n  border-color: #45B649;\r\n  color: #3f7f34;\r\n  font-weight: 600;\r\n}\r\n.btn-outline-primary:hover {\r\n  background: #45B649;\r\n  color: #ffffff;\r\n}\r\n.background-hover:hover {\r\n  background-color: #50fa7b33 !important;\r\n}\r\nbody.dark .background-hover:hover {\r\n  background-color: #ffa60033 !important;\r\n}\r\n\r\n/* Close buttons */\r\n.custom-close-button {\r\n  background-color: transparent;\r\n  border: 0;\r\n  border-radius: 0.25rem;\r\n  white-space: nowrap;\r\n  text-decoration: none;\r\n  padding: 0.25rem 0.5rem;\r\n  margin: 0.25rem;\r\n  cursor: pointer;\r\n}\r\n.custom-close-button:hover,\r\n.custom-close-button:focus {\r\n    background: transparent;\r\n}\r\n.custom-close-button:focus {\r\n    outline: 1px solid transparent;\r\n    outline-offset: -4px;\r\n}\r\n.custom-close-button:active {\r\n    transform: scale(0.99);\r\n}\r\n.btn-outline-orange {\r\n  border-color: #FF9500;\r\n  color: #1a1a1a;\r\n}\r\n.btn-outline-orange:hover {\r\n  background: #FF9500;\r\n  color: #1a1a1a;\r\n}\r\nbody.dark .btn-outline-orange {\r\n  border-color: #ffa600;\r\n  color: #ffa600;\r\n}\r\nbody.dark .btn-outline-orange:hover {\r\n  background: #FF9500;\r\n  color: #1a1a1a;\r\n}\r\n\r\n/* Secondary buttons */\r\nbody:not(.dark) .btn-secondary {\r\n  background-color: #8B9196;\r\n  border-color: #8B9196;\r\n}\r\nbody:not(.dark) .btn-secondary:hover,\r\nbody:not(.dark) .btn-secondary:active {\r\n  background-color: #6c757d;\r\n  border-color: #6c757d;\r\n}\r\n\r\n/* Small light buttons */\r\n.btn-sm-light {\r\n  padding: 3px 12px;\r\n  font-size: 12px;\r\n  border: 1px solid rgb(27 31 35 / 15%);\r\n  line-height: 20px;\r\n  background-color: #fafbfc;\r\n  transition: .2s cubic-bezier(.3, 0, .5, 1);\r\n  transition-property: color,background-color,border-color;\r\n}\r\n.btn-md-light {\r\n  padding: 4px 8px;\r\n  font-size: 14px;\r\n  line-height: 21px;\r\n}\r\n.btn-sm-light:hover {\r\n  background-color: #f3f4f6;\r\n  border-color: rgb(27 31 35 / 15%);\r\n}\r\nbody.dark .btn-sm-light {\r\n  background-color: #30363d;\r\n  border-color: #8b949e;\r\n  color: #c9d1d9;\r\n}\r\nbody.dark .btn-sm-light:hover {\r\n  background-color: #282e33;\r\n  border-color: #6e7681;\r\n}\r\n.btn-set-width {\r\n  width: 77px;\r\n}\r\n\r\n/* Links */\r\n.text-link {\r\n  color: #e53a23 !important;\r\n}\r\n.text-link:hover {\r\n  color: #FF9500 !important;\r\n}\r\nbody.dark .text-link {\r\n  color: #ffa600 !important;\r\n}\r\nbody.dark .text-link:hover {\r\n  color: #FF9500 !important;\r\n}\r\n\r\n/* Dark actions */\r\nbody.dark .btn-outline-dark {\r\n  border-color: #ccc;\r\n  color: #ccc;\r\n}\r\nbody.dark .btn-outline-dark:hover {\r\n  border-color: #fff;\r\n  color: #fff;\r\n}\r\nbody.dark .btn-outline-primary {\r\n  border-color: #50fa7b;\r\n  color: #50fa7b;\r\n}\r\nbody.dark .btn-outline-primary:hover {\r\n  background: #50fa7b;\r\n  color: #1a1a1a;\r\n}\r\nbody.dark .feather-search {\r\n  stroke: #ccc;\r\n}\r\nbody.dark .paypal-button-text {\r\n  color: #ccc !important;\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Alerts\r\n *------------------------------------------------*/\r\n\r\n/* Warning alert */\r\n.alert a {\r\n  color: inherit !important;\r\n  text-decoration: underline !important;\r\n}\r\n.alert a:hover {\r\n  color: #000000 !important;\r\n}\r\n\r\n/* Informational alert */\r\n.info-bar {\r\n  background-color: #f8f8f8;\r\n}\r\nbody.dark .info-bar {\r\n  background-color:rgba(255, 255, 255,.15);\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Breadcrumbs\r\n *------------------------------------------------*/\r\n\r\n.breadcrumb-item+.breadcrumb-item::before {\r\n  content: \">\";\r\n}\r\n.breadcrumb-item,\r\n.breadcrumb-item .action {\r\n  color: #1a1a1a !important;\r\n}\r\n.breadcrumb-item .action:hover {\r\n  color: #198754 !important;\r\n  text-decoration: underline !important;\r\n}\r\nbody.dark .breadcrumb-item,\r\nbody.dark .breadcrumb-item .action {\r\n  color: #f5f5f5 !important;\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Backgrounds\r\n *------------------------------------------------*/\r\n\r\n/* Gradient background */\r\n.bg-decorative {\r\n  background-color: #062A00;\r\n  background-image: -webkit-linear-gradient(-45deg, #2d47a3ff, #90d3bcff);\r\n  background-size: contain;\r\n  background-repeat: no-repeat;\r\n}\r\n\r\n/* Loading indicator */\r\n@keyframes placeHolderShimmer{\r\n  0% { background-position: -468px 0 }\r\n  100% { background-position: 468px 0 }\r\n}\r\n.linear-background {\r\n  animation-duration: 1s;\r\n  animation-fill-mode: forwards;\r\n  animation-iteration-count: infinite;\r\n  animation-name: placeHolderShimmer;\r\n  animation-timing-function: linear;\r\n  background: #f6f7f8;\r\n  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);\r\n  background-size: 1000px 104px;\r\n  height: 200px;\r\n  position: relative;\r\n  overflow: hidden;\r\n}\r\nbody.dark .linear-background {\r\n  background: linear-gradient(90deg,#eeeeee30 8%,#dddddd30 18%,#eeeeee30 33%);\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Cards\r\n *------------------------------------------------*/\r\n\r\n.card-footer {\r\n  background-color: transparent;\r\n}\r\n.shadow-hover:hover {\r\n  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);\r\n}\r\n.shade-hover:hover {\r\n  background-color: #f8f9fa;\r\n}\r\nbody.dark .card.shade-hover {\r\n  background-color: #323443 !important;\r\n}\r\nbody.dark .shade-hover:hover {\r\n  background-color: #44475a !important;\r\n}\r\n.gold-shadow {\r\n  box-shadow: 0 .75rem 1rem #BF953F !important;\r\n}\r\n\r\n/* Dark cards */\r\nbody.dark .card-body,\r\nbody.dark .card-footer {\r\n  background-color: #333644;\r\n}\r\nbody.dark .card {\r\n  border-color: #dee2e6;\r\n}\r\nbody:not(.dark) .card {\r\n  color: #f5f5f5;\r\n  border-color: #bbbbbb !important;\r\n}\r\nbody.dark .card {\r\n  background-color: #1a1a1a;\r\n}\r\nbody.dark .card-header {\r\n  background-color: #323443;\r\n}\r\nbody.dark .card-body {\r\n  background-color: #616161;\r\n}\r\n.bg-white-dynamic {\r\n  background-color: #fff;\r\n}\r\nbody.dark .bg-white-dynamic {\r\n  background-color: #281e52;\r\n}\r\nbody.dark .bg-light {\r\n  background-color: #323443 !important;\r\n}\r\nbody.dark label {\r\n  color: #f5f5f5;\r\n}\r\nbody.dark p.lead a {\r\n  color: #45B649;\r\n}\r\nbody.dark p.lead a:hover {\r\n  color: #96e6a1;\r\n}\r\n\r\n/*--------------------------------------------------\r\n * Forms\r\n *------------------------------------------------*/\r\n\r\n/* Form layouts */\r\n.form-group {\r\n  margin-bottom: 1rem;\r\n}\r\n.form-control {\r\n  border-color: #777;\r\n}\r\n.form-control-light {\r\n  border-color: rgba(27,31,35,.15);\r\n}\r\n.input-group-text {\r\n  background-color: #fafbfc;\r\n}\r\nbody.dark .input-group-text {\r\n  color: #ccc;\r\n  background-color: #30363d;\r\n}\r\n.form-control:focus {\r\n  border-color: #96e6a1;\r\n  box-shadow: 0 0 0 0.2rem #96e6a140;\r\n}\r\n.form-label-group {\r\n  position: relative;\r\n  margin-bottom: 1rem;\r\n}\r\n.form-label-group input,\r\n.form-label-group label {\r\n  height: 3.125rem;\r\n  padding: .75rem;\r\n}\r\n.form-label-group label {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  display: block;\r\n  width: 100%;\r\n  margin-bottom: 0; /* Override default `<label>` margin */\r\n  line-height: 1.5;\r\n  color: #495057;\r\n  pointer-events: none;\r\n  cursor: text; /* Match the input under the label */\r\n  border: 1px solid transparent;\r\n  border-radius: .25rem;\r\n  transition: all .1s ease-in-out;\r\n}\r\n.form-label-group input::-webkit-input-placeholder {\r\n  color: transparent;\r\n}\r\n.form-label-group input::-moz-placeholder {\r\n  color: transparent;\r\n}\r\n.form-label-group input:-ms-input-placeholder {\r\n  color: transparent;\r\n}\r\n.form-label-group input::-ms-input-placeholder {\r\n  color: transparent;\r\n}\r\n.form-label-group input::placeholder {\r\n  color: transparent;\r\n}\r\n.form-label-group input:not(:-moz-placeholder-shown) {\r\n  padding-top: 1.25rem;\r\n  padding-bottom: .25rem;\r\n}\r\n.form-label-group input:not(:-ms-input-placeholder) {\r\n  padding-top: 1.25rem;\r\n  padding-bottom: .25rem;\r\n}\r\n.form-label-group input:not(:placeholder-shown) {\r\n  padding-top: 1.25rem;\r\n  padding-bottom: .25rem;\r\n}\r\n.form-label-group input:not(:-moz-placeholder-shown) ~ label {\r\n  padding-top: .25rem;\r\n  padding-bottom: .25rem;\r\n  font-size: 12px;\r\n  color: #777;\r\n}\r\n.form-label-group input:not(:-ms-input-placeholder) ~ label {\r\n  padding-top: .25rem;\r\n  padding-bottom: .25rem;\r\n  font-size: 12px;\r\n  color: #777;\r\n}\r\n.form-label-group input:not(:placeholder-shown) ~ label {\r\n  padding-top: .25rem;\r\n  padding-bottom: .25rem;\r\n  font-size: 12px;\r\n  color: #777;\r\n}\r\n@supports (-ms-ime-align: auto) {\r\n  .form-label-group {\r\n    display: -ms-flexbox;\r\n    display: flex;\r\n    -ms-flex-direction: column-reverse;\r\n    flex-direction: column-reverse;\r\n  }\r\n  .form-label-group label {\r\n    position: static;\r\n  }\r\n  .form-label-group input::-ms-input-placeholder {\r\n    color: #777;\r\n  }\r\n}\r\n\r\n/* Dark forms */\r\nbody.dark .form-check-input:checked {\r\n  background-color: rgb(29 233 182);\r\n  border-color: rgb(29 233 182);\r\n}\r\nbody.dark textarea,\r\nbody.dark textarea:focus {\r\n  color: #fff;\r\n  background: #333644;\r\n}\r\nbody.dark .form-select,\r\nbody.dark .form-control {\r\n  background: #333644;\r\n  border-color: #9b9ca3;\r\n  color: #ccc;\r\n}\r\nbody.dark footer .form-control {\r\n  background: #fff;\r\n 